.login {
  margin-top: 4%;
}
h1 {
  display: flex;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  justify-content: center;
  /* text-decoration: underline double; */
}
.form {
  /* border:1px solid green; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-container {
  margin: 2% 2% 1%;
  width: auto;
  display: flex;
  flex-direction: column;
  /* border: 1px solid black; */
  width: 20%;
  justify-content: center;
  align-items: center;
}
#userName {
  margin: 2% 0% 2%;
  width: 90%;
  /* border: 1px solid black; */
}
#password {
  margin: 2% 0% 2%;
  width: 90%;
  /* border: 1px solid black; */
}
#email {
  margin: 2% 0% 2%;
  width: 90%;
  /* border: 1px solid black; */
}
span {
  font-size: 25px;
  font-family: cursive;
}
input {
  font-size: 25px;
  font-family: cursive;
  text-emphasis: none;
  display: flex;
  width: 90%;
  margin: 1% 0% 1%;
  border: none;
  border-bottom: 1px dotted black;
  background: rgb(177, 245, 198);
}
button {
  font-size: 25px;
  margin: 0% 0% 2%;
  background-color: #4caf4e; /* Green */
  border: none;
  color: rgb(54, 32, 32);
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  border-radius: 5px;
  font-size: 16px;
}
button:hover{
    cursor: pointer;
    padding: 12px 34px;    

}
