.nav{
  position: sticky;
  top:0;
  z-index: 100;
}
.container{
  margin: 0;
  border: 1px solid black;
  background: rgb(51, 27, 27);
  background-color:black;
}
.menus {
  /* padding: 1em 2em 1em; */
  list-style: none;
  display: flex;
  background: black;
  font-size: 20px;
}
.menus1 {
  /* background: green; */
  display: flex;
  justify-content: flex-start;
  width: 100%;
}
.menus2 {
  /* background: rgb(1, 46, 1); */
  display: flex;
  justify-content: flex-end;
  width: 100%;
}
#menu{
  text-decoration: none;
  color:aliceblue;
  border-radius: 3px;
  margin:0% 2% 0%;
  padding:1% 1.5% 1%;
  width: 10%;
  align-items: center;
  display: flex;
  justify-content: flex-end;
}
#menu:hover{
  cursor: pointer;
}
nav a.active{
  font-weight: bold;
  background: black;
  border-bottom: 2px solid whitesmoke;
}